// Config
@import '../config/breakpoints';

// Utils
@import '../utils/functions/rem';
@import '../utils/mixins/aspect-ratio';

/**
 * Components / Product Excerpt
 */
.product-excerpt {
    margin-top: 100px;
    height: auto;
    text-align: center;
    border: 1px solid transparent;
    transition: border-color 200ms ease;

    @media screen and (max-width: $breakpoint-mobile-portrait) {
        margin-top: 30vh;
        width: 100%;
    }

}

.product-excerpt__inner {

    @include aspect-ratio(1, 1);


    .product-excerpt__info {

        top: 70%;
        z-index: 1000;
        position: absolute;
        left: 50%;
        bottom: 0;
        width: 50%;
        transform: translateX(-50%);
        bottom: -100px;
        
    }

    @media screen and (max-width: $breakpoint-palm-portrait) {
        &::before {
            padding-top: 75%;
        }

        h4 {
            bottom: -200px;
        }
    }

}


.woocommerce-notices-wrapper {

    display: none;

}

.product-excerpt__image {
    width: 300px !important;
    height: auto;
    margin: auto;

    @media screen and (max-width: $breakpoint-tablet-portrait) {
        height: auto;
        width: 50vw;
        margin-left: 50%; 
        transform: translateX(-50%);
    }

    @media screen and (max-width: $breakpoint-mobile-landscape) and (min-width: $breakpoint-mobile-portrait) {
        height: auto;
        width: 100vw;
        margin-left: 50%; 
        transform: translateX(-50%);
    }

    @media screen and (max-width: $breakpoint-mobile-portrait) {
        height: auto;
        width: 100vw;
        margin-left: 50%; 
        transform: translateX(-50%);
    }

}

.product-excerpt__name,
.product-excerpt__price {
    display: block;
}

.product-excerpt__name {
    font-size: rem(20px);
    font-weight: 700;
    color: black;

    @media screen and (max-width: $breakpoint-mobile-landscape) {
        font-size: rem(22px);
    }

}

.product-excerpt__price {
    margin-top: 10px;
    font-size: rem(18px);
    font-weight: 400;
    font-family: $fontstack-sans-primary;
    .product-buy {
        float: right;
    }
    padding: 0 20%;
}

.product-excerpt__description {
    color: #888888; 
    font-size: rem(15px);
}